import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/particles';

export const apiParticlesGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiParticlesPost = async (serviceUrl, particles) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: particles ? JSON.stringify(particles) : null,
  };
  return request(url, options);
};

export const apiParticlesPut = async (serviceUrl, id, particles) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: particles ? JSON.stringify(particles) : null,
  };
  return request(url, options);
};

export const apiParticlesDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
